import vector from '../math/Vector';

const Point = {
    init: function(x, y) {
        this.position = vector(x, y);
        this.type = 'point';
        this.constraints = [];
    }
};

const point = function(x, y) {
    let p = Object.create(Point);
    p.init(x, y);
    return p;
};

export default point;



